import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import { getBasename, setBasenameInLocalStorage, getBasenameInLocalStorage } from './config/getBasename';
import Login from './Pages/Login.jsx';
import Layout from './Pages/Layout.jsx';
import Alterar_senha from './Pages/Alterar_senha.jsx';
import Pagina_erro from './Pages/Erro/Pagina_nao_encontrada.jsx';
import Home from './Components/Comum/Dashboard.jsx';

const basename = getBasename();
setBasenameInLocalStorage(basename);

const App = () => {
  return (
    <BrowserRouter>
      <Routes basename={basename}>
        <Route path="/testes/login" exact element={<Login />} />
        <Route path="/testes/home" element={<Home />} />
        <Route path="/testes/nova-senha" element={<Alterar_senha />} />
        <Route path="/testes/pagina-nao-encontrada" element={<Pagina_erro />} />
        <Route path="*" element={<Navigate to="/testes/pagina-nao-encontrada" />} />
      </Routes>
    </BrowserRouter>
  );
};

ReactDOM.createRoot(document.getElementById('root')).render(
  <App />
);